/* Глобальні стилі для секцій, контейнеру, заголовків та тексту секцій */

body {
  background-color: $registration-bg-color;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
}

body.modal-open {
  overflow: hidden;
}

ul,
h1,
h2,
h3,
h4,
h5,
p {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  // border: 1px solid red;

  @include for-size(phone-up) {
    width: 320px;
  }

  @include for-size(tablet-up) {
    width: 768px;
  }

  @include for-size(desktop-up) {
    width: 1280px;
  }
}

// заголовок секций для моб и таблет

.section-title {
  font-family: Caveat Brush;
  font-size: 24px;
  line-height: 1.65;
  text-align: center;
  color: $primary-text-color;

  @include for-size(tablet-up) {
    font-size: 36px;
    line-height: 1.81;
    text-align: center;
  }
}

.button {
  // display: block;
  width: 145px;
  padding: 0;
  margin: 0;
  margin-right: auto;
  margin-left: auto;
  font-family: Caveat Brush;
  font-size: 18px;
  line-height: 2.25;
  // text-align: center;
  cursor: pointer;
  border: none;
  color: $title-button-color;
  background-color: $bg-button-color;
  border-radius: 25px;

  &:focus,
  &:hover {
    border: 2px solid $bg-button-color;
    background-color: $title-button-color;
    color: $bg-button-color;
    box-shadow: 0px 4px 4px rgba(171, 129, 108, 0.25);
  }

  @include for-size(desktop-up) {
    width: 158px;
    font-size: 24px;
    // line-height: 2.71;
  }
}
